@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
   font-family: "Poppins", sans-serif;
 
}
body{
  margin: 0;
  background-image: linear-gradient(-225deg, #D4FFEC 0%, #57F2CC 48%, #4596FB 100%);
  box-sizing: border-box;

 
}

.container{
  object-fit: contain;
  width:100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
 
}
.image{
  object-fit:cover;
 margin: 30px 0px;
  border-radius: 10px;
 
}
input{
padding:20px;
margin: 10px;
width:450px;
border-radius: 5px;
border:2px solid rgb(2, 159, 176) ;
outline: none;
font-size: 15px;

}
.label-box{
  display: inline-block;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 15px;
  

}
.button{
  padding:15px 30px;
  border:none;
  margin-top: 10px;
  border-radius: 10px;
  outline: none;
  margin-inline: 20px; ;
  background: rgb(2, 93, 2);
  color:white;
  font-size: 15px;
  cursor: pointer;
}
.one{
  background: #013f60;
  
}
.foot{
  display: flex;
  
}
.one:hover{
  background: #023551;
}
.two:hover{
  background:rgb(2, 56, 2) ;
}
.foot a{
 color:rgb(49, 1, 46);
}